.contact {
    color: #333;
    background: #FFFFFF;
    box-shadow: 0 4px 8px rgba(0,0,0,0.1);
    font-size: 18px;
    padding: 20px;
    margin: 20px;
    border-radius: 8px;
    font-family: 'Gudea', sans-serif;
    text-align: justify;
    text-justify: inter-word;
}

.contact h2 {
    text-align: center;
    color: #1B5E20;
}

.contact_content {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.contact_content_text {
    padding: 20px;
}

.contact_content_image {
    padding: 20px;
}

.contact_content_image img {
    width: 300px;
    height: 200px;
    border-radius: 10px;
    box-shadow: 5px 10px 8px #888888;
}