.home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: justify;
    text-justify: inter-word;
    color: #ffffff;
    background-color: rgba(0, 0, 0, 0.5); /* Semi-transparent black background for content */
    font-size: 18px;
    padding: 20px;
    margin: 20px;
    border-radius: 8px;
    font-family: 'Gudea', sans-serif;
}

.home h2 {
    text-align: center;
}

.home_content {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.home_content_text {
    padding: 20px;
}

.home_content_image {
    padding: 20px;
}

.home_content_image img {
    width: 300px;
    height: 200px;
    border-radius: 10px;
    box-shadow: 5px 10px 8px #888888;
}

.home h2 {
    text-align: center;
    color: #1B5E20;
}

.home_content {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.home_content_text {
    padding: 20px;
    line-height: 1.6;
}

.home_content_image {
    padding: 20px;
}

.home_content_image img {
    width: 300px;
    height: 200px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0,0,0,0.15);
}