    font-size: 18px;
    padding: 20px;
    margin: 20px;
    border-radius: 8px;
    font-family: 'Gudea', sans-serif;
    text-align: justify;
    text-justify: inter-word;
}

.published h2 {
    text-align: center;
    color: #1B5E20;
}